
let VideoTemplate=(props)=>{
    return<div>
        <div className="card w-100">
            <div className="card-body">
                <h1 className="Link">
                    {props.name} - {props.compositor}
                </h1>
                <video controls="controls">
                    <source src={props.vsrc}>
                    </source>
                </video>
            </div>
        </div>
    </div>
}
export default VideoTemplate